function* fibonacci (n) {
    let [a, b] = [0, 1];
    let i = 0;
    while (i < n) { 
        [a, b] = [b, a + b];
        i++;
        console.log(a, b);
        yield a;
    }
}

var fib = fibonacci(10);
console.log(fib.next());
console.log(fib.next());
console.log(fib.next());
